            </div>
          </div>
        </div>
        <div class="content-body-right-box right">
          <div class="content-body-right-header">
            <span class="icon-save"></span>
            <span class="header-name">已发布历史（{{total}}）</span>
          </div>
          <div class="scroll-box">
            <div class="content-body-right-items-box">
              <div class="item-box" v-for="(item,index) in historyList" :key="item.id">
                <div class="item-time"><span class="blue-point"></span> {{item.update_time}}
                  <span class="item-time-right" @click="delHistory(item.id)">删除</span>
                  <span class="item-time-right" @click="viewFileClick(item.id)">编辑</span>
                </div>
                <div class="item-text">{{item.title | texteclipsis(60)}}</div>
              </div>
