</template>
<script>
import {
  allProjectOrFund,
  fundRelate,
  fundShortNews,
  fundShortHis,
  fundNewsInfo,
  delFundShort,
  delNewsFile
} from '../../config/api'
import progressBar from '../../components/progressbar.vue'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      commitData: {
        title: '',
      },
      isEditing: false, //是否正在修改
      loading: false,
      historyList: [],
      companyList: [],
      totalCompanyList: [],

      oldFile: [],

      keyWord: '',
      page: 1,
      number: 20,
      total: 0,
      type: 1, //1=>新建。2=>查看
      historyId: 0,
      historyDetail: '',
      countTitle: [{
        name: '已投项目',
        value: 'already'
      }, {
        name: '全部退出',
        value: 'quitAll'
      }, {
        name: '部分退出',
        value: 'quitPart'
      }, {
        name: '已挂牌/已报会',
        value: 'listed'
      }, {
        name: '拟挂牌',
        value: 'planList'
      }, {
        name: '本月投资项目',
        value: 'invest_month'
      }, {
        name: '本月退出项目',
        value: 'quit_month'
      }],
      companyId: '', //基金id
      companyVal: '', //基金名称
      newFile: [], //要上传的文件
      countList: '', //项目概况
      title: '', //简报标题
      delOldFilelist: [], //要删除的历史文件
    }
  },
  components: {
    progressBar
  },
  mounted() {},
  created() {
    this.initData();
  },
  computed: {
    submitOk: function() {
      if (this.title != '' && this.companyId != '') {
        return true
      } else {
        return false
      }
    }
  },
  methods: {
    initData() {
      this.historyList = [];
      this.getHistory();
      this.getCompanyList();
    },
    //清除数据
    clearData() {
      this.delOldFilelist = [];
      this.companyId = ''; //基金id
      this.companyVal = ''; //基金名称
      this.newFile = []; //要上传的文件
      this.oldFile = []; //之前的文件
      this.countList = ''; //项目概况
      this.title = ''; //简报标题
      this.delOldFilelist = []; //要删除的历史文件
      this.isEditing = false; //是否正在修改
    },
    //search获取列表
    getDatalist() {
      this.page = 1;
      this.getHistory();
    },
    //提交
    commitDataClick() {
      if (this.submitOk) {
        let param = {
          fund_id: this.companyId,
          title: this.title,
        };
        for (let i in this.countList) {
          param[i] = this.countList[i]
        }
        if (this.type == 2) {
          param.id = this.historyId;
        }
        fundShortNews({
          data: JSON.stringify(param)
        }, this.newFile).then(res => {
          if (res.data.msgNo == 1) {
            this.delOldFileCommit();
            dialogConfirm('success', '发布成功');
            this.clearData();
            this.initData();
          } else {
            dialogConfirm('fail', res.data.message)
          }
        })
      } else {

      }


    },
    createFile() {
      dialogConfirmHasbtn('确定要离开编辑页面吗？').then(() => {
        this.type = 1;
        this.initData();
        this.clearData();
      }).catch(() => {});

    },
    getHistory() {
      fundShortHis({
        search: this.keyWord,
        page: this.page,
        number: this.number
      }).then(res => {
        if (res.data.msgNo == 1) {
          this.historyList = this.historyList.concat(res.data.payload.data);
          this.total = res.data.payload.total;
        }
      })
    },
    loadMore() {
      if (this.historyList.length < this.total && this.historyList.length != 0) {
        this.page++;
        this.getHistory()
      }
    },
    //历史详情
    getHistoryDetail() {
      fundNewsInfo({
        id: this.historyId
      }).then(res => {
        if (res.data.msgNo == 1) {
          let hd = res.data.payload.data;
          this.countList = {
              invest_month: hd.invest_month,
              listed: hd.listed,
              planList: hd.planList,
              quitAll: hd.quitAll,
              quitPart: hd.quitPart,
              quit_month: hd.quit_month,
              already: hd.already
            },
            this.companyId = hd.fund_id;
          this.companyVal = hd.fundName;
          this.title = hd.title;
          this.oldFile = res.data.payload.file;
        }
      })
    },
    //删除历史
    delHistory(val) {
      dialogConfirmHasbtn('确定要删除改发布历史吗？').then(() => {
        delFundShort({
          id: val
        }).then(res => {
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '删除成功！')
          } else {
            dialogConfirm('success', '删除失败！')
          }
          //this.type = 1;
          this.page = 1;
          this.initData();
        })
      }).catch(() => {});

    },
    addFile(e, i) {
      for (let val of e.target.files) {
        this.newFile.push(val);
      }
      if (i != -1) {
        this.delFile(i);
      }
      e.target.value = '';
    },
    delFile(i) {
      this.newFile.splice(i, 1);
    },
    //删除原有文件
    delOldFile(id, i) {
      this.oldFile.splice(i, 1);
      this.delOldFilelist.push(id);
    },
    //删除服务器原有文件
    delOldFileCommit() {
      if (this.delOldFilelist.length > 0) {
        this.delOldFilelist.forEach(item => {
          delNewsFile({
            file_id: item
          }).then(res => {})
        })
      }
    },
    //显示历史详情
    viewFileClick(index) {
      this.type = 2;
      this.historyId = index;
      this.getHistoryDetail()
    },


    //选择公司
    getCompanyList() {
      //获取公司列表
      allProjectOrFund({
        type: 2
      }).then(res => {
        if (res.data.msgNo == 1) {
          this.totalCompanyList = res.data.payload.data;
          this.companyList = this.totalCompanyList.slice(0, 20);
        }
      })
    },
    changeCompany(e) {
      this.companyId = this.companyVal;
      this.getCompanyDetail();
      if (this.companyVal != '') {
        let a = '';
        this.companyList.forEach(item => {
          if (this.companyId == item.id) {
            a = item.name
          }
        });
        let mm = new Date().getMonth();
        this.title = mm + '月' + a + '简报'
      }
    },
    getCompanyDetail() {
      if (this.companyId != '') {
        fundRelate({
          fund_id: this.companyId
        }).then(res => {
          if (res.data.msgNo == 1) {
            let cl = res.data.payload;
            for (let k in cl) {
              if (cl[k] == "" || cl[k] == null) {
                cl[k] = 0
              }
            }
            this.countList = cl;
          }
        })
      }
    },
    ////公司搜索
    remoteMethod(query) {
      if (query !== '') {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.companyList = this.totalCompanyList.filter(item => {
            return item.name.indexOf(query) > -1;
          });
        }, 200);
      } else {
        this.companyList = [];
      }
    },
  }
};

</script>
