  top: 0px;
  width: 100%;
  height: 100%;
  cursor: pointer;
  opacity: 0;
}

.file-add>i {
  line-height: 36px;
  margin-right: 10px;
  color: #B0B0B0;
}

.file-add:hover {
  color: #fff;
  background-color: #3C98E8;
  &>i {
    color: #fff;
  }
}

.icon-save {
  display: inline-block;
  width: 20px;
  height: 20px;
  background: url('../../images/investDetail_history.png') no-repeat center;
  background-size: 100%;
}

@media (max-width: 1350px) {
  .content-body-left-box {
    right: 280px;
  }
  .content-body-right-box {
    width: 280px;
  }
}

